
import { Options, Vue } from "vue-class-component";

@Options({
  props: {
    title: {
      type: String,
      default: "",
    },
    show: Boolean,
    cancel: {
      type: String,
      default: "",
    },
    color: {
      type: String,
      default: "#000",
    },
    ok: {
      type: String,
      default: "确定",
    },
  },
})
export default class Modal extends Vue {
  show!: boolean;
  onCancel() {
    this.$emit("update:show", false);
    this.$emit("onCancel");
  }
  onOk() {
    this.$emit("update:show", false);
    this.$emit("onOk");
  }
}
